import { Component } from '@angular/core';
import { MenuController, NavController, IonicPage } from 'ionic-angular';
import { EneroPage } from '../enero/enero';

@IonicPage()
@Component({
  selector: 'page-side-menu',
  templateUrl: 'side-menu.html'
})
export class SideMenuPage {

  MENU = {
    DEFAULT: 'menu-components',
    MATERIAL: 'menu-material',
    AVATAR: 'menu-avatar',
    DARK: 'menu-dark',
    RIGHT: 'menu-right',
  };

  constructor(public navCtrl: NavController, public menuCtrl: MenuController) { }

  goEnero(){
    console.log("goEnero");
    this.navCtrl.push(EneroPage);
  }
 

   ionViewWillEnter() {
     this.menuCtrl.enable(true, 'menu-material');
   }
  
   ionViewWillLeave() {
     this.menuCtrl.enable(false, 'menu-material');
   }


}



